import React from 'react'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import AddIcon from '@material-ui/icons/Add'
import { Link } from "react-router-dom"

import COLORS from 'ats/constants/colors'
import SimpleDialog from 'ats/views/basic/dialog/SimpleDialog'
import navigator from 'ats/navigator'

interface IProps {
  isOpen: boolean
  onClose: () => void
  onSelectFilter: (filerIndex: number) => void
}

function stopEventPropagation(event: any) {
  event.stopPropagation()
}

function FilterSelectDialog({ isOpen, onClose, onSelectFilter }: IProps) {
  const handleSelectFilter = (filterIndex: number) => {
    onSelectFilter(filterIndex)
    onClose()
  }

  return (
    <SimpleDialog
      fullWidth
      maxWidth="md"
      isOpen={isOpen}
      onClose={onClose}
    >
      <Content>
        <Title>Select Filter</Title>
        <FilterListContainer>
          <FilterContainer onClick={() => { handleSelectFilter(0) }}>
            <ListIndexSpan>1.</ListIndexSpan>
            <FilterNameSpan>No filter</FilterNameSpan>
          </FilterContainer>
          <FilterContainer onClick={() => { handleSelectFilter(1) }}>
            <ListIndexSpan>2.</ListIndexSpan>
            <FilterNameSpan>Filter for Store Manager</FilterNameSpan>
            <FilterLinkContainer onClick={stopEventPropagation}><Link to={navigator.filter(1)}>view details</Link></FilterLinkContainer>
          </FilterContainer>
        </FilterListContainer>

        <NewFilterButtonContainer>
          <Button variant="contained">
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
            >
              <AddIcon />
              <NewFilterButtonText>Create New Filter</NewFilterButtonText>
            </Grid>
          </Button>
        </NewFilterButtonContainer>
      </Content>
    </SimpleDialog>
  )
}

const Content = styled.div`
  padding: 20px;
`

const Title = styled.div`
  color: ${COLORS.Steel};
  font-size: 35px;
  font-weight: 600;
  padding-left: 10px;
`

const FilterListContainer = styled.div`
  padding: 30px 0 30px 40px;
  font-size: 20px;
`

const FilterContainer = styled.div`
  margin-right: 40px;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  :hover {
    background-color: ${COLORS.Porcelain}
  }
`

const ListIndexSpan = styled.span`
  font-size: 12px;
  font-style: italic;
  padding-right: 10px;
  color: ${COLORS.Steel};
`

const FilterNameSpan = styled.span`
  font-size: 22px;
  color: ${COLORS.Chalkboard};
`

const FilterLinkContainer = styled.span`
  font-size: 14px;
  font-style: italic;
  padding-left: 30px;
`

const NewFilterButtonContainer = styled.div`
  padding: 20px 0 40px 60px;
`

const NewFilterButtonText = styled.div`
  padding-left: 10px;
  color: ${COLORS.Chalkboard};
  font-size: 12px;
`

export default FilterSelectDialog
