import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import AddIcon from '@material-ui/icons/Add'

import { IApplicant, IApplicantScores } from 'ats/types/applicant'
import { IEnhancedTableHeadData, IEnhancedTableRowData } from 'ats/types/table'

import COLORS from 'ats/constants/colors'
import EnhancedTable from 'ats/views/basic/table/EnhancedTable'
import PieChart from 'ats/views/basic/chart/PieChart'
import { selectAllApplicants, selectApplicantById } from 'ats/slices/applicant'
import { setIsFilterSelected, selectAllFilters } from 'ats/slices/filter'
import { getApplicantScores } from 'ats/util'
import ApplicantDetailDialog from 'ats/pages/Filter/ApplicantDetailDialog'

import FilterSelectDialog from './FilterSelectDialog'

const nameColHeader: IEnhancedTableHeadData = { columnKey: 'name', align: 'left', padding: 'none', label: 'Name' }
const paredScoreColHeader: IEnhancedTableHeadData = { columnKey: 'paredScore', align: 'right', padding: 'default', label: 'Pared Score' }
const otherColHeader: IEnhancedTableHeadData [] = [
  { columnKey: 'applyTo', align: 'right', padding: 'default', label: 'Apply To' },
  { columnKey: 'latestExperience', align: 'right', padding: 'default', label: 'Latest Experience' },
  { columnKey: 'averageTurnOver', align: 'right', padding: 'default', label: 'Ave Turn Over (Years)' },
  { columnKey: 'jobSource', align: 'right', padding: 'default', label: 'Job Source' },
  { columnKey: 'applyAt', align: 'right', padding: 'default', label: 'Apply At' },
]

const tableHeadWithoutParedScore: IEnhancedTableHeadData [] = [
  nameColHeader,
  ...otherColHeader
]

const tableHeadWithParedScore: IEnhancedTableHeadData [] = [
  nameColHeader,
  paredScoreColHeader,
  ...otherColHeader
]

function NewApplicants() {
  const [isApplicantDetailDialogOpen, setIsApplicantDetailDialogOpen] = useState(false)
  const [isFilterSelectDialogOpen, setIsFilterSelectDialogOpen] = useState(false)
  const [selectedApplicantId, setSelectedApplicantId] = useState<number>(0)
  const [matchedApplicants, setMatchedApplicants] = useState<IApplicant []>([])
  const [applicantScores, setApplicantScores] = useState<{ [id: number]: IApplicantScores }>({})
  const newApplicants = useSelector(state => selectAllApplicants(state))
  const allFilters = useSelector(selectAllFilters)
  const selectedApplicant = useSelector(state => selectApplicantById(state, selectedApplicantId)) || null
  const dispatch = useDispatch()

  useEffect(() => {
    const applicantScores = getApplicantScores(newApplicants, allFilters)

    let sortedMatched: IApplicant []
    if (allFilters.isFilterSelected) {
      const matched = newApplicants.filter((applicant) => {
        const score = applicantScores[applicant.id]
        if (score) {
          return (score.overallScore >= allFilters.minScore && score.overallScore <= allFilters.maxScore)
        } else {
          return false
        }
      })

      sortedMatched = matched.sort((first, second) => {
        const firstScore = applicantScores[first.id]
        const secondScore = applicantScores[second.id]
        if (!firstScore) { return 1 }
        if (!secondScore) { return -1 }
        return secondScore.overallScore - firstScore.overallScore
      })
    } else {
      sortedMatched = newApplicants
    }

    setMatchedApplicants(sortedMatched)
    setApplicantScores(applicantScores)
  }, [newApplicants, allFilters])

  const closeApplicantDetailDialog = () => {
    setIsApplicantDetailDialogOpen(false)
  }

  const openFilterSelectDialog = () => {
    setIsFilterSelectDialogOpen(true)
  }

  const closeFilterSelectDialog = () => {
    setIsFilterSelectDialogOpen(false)
  }

  const onSelectFilter = (filterIndex: number) => {
    if (filterIndex > 0) {
      dispatch(setIsFilterSelected({ isSelected: true }))
    } else {
      dispatch(setIsFilterSelected({ isSelected: false }))
    }
  }

  const onSelectApplicant = (event: any, applicantId: number) => {
    if (allFilters.isFilterSelected) {
      setSelectedApplicantId(applicantId)
      setIsApplicantDetailDialogOpen(true)
    }
  }

  const tableRows: IEnhancedTableRowData [] = []

  const applyToData: { [position: string]: number } = {}
  const latestExperienceData: { [position: string]: number } = {}
  const jobSourceData: { [source: string]: number } = {}

  matchedApplicants.forEach((applicant) => {
    const latestExperience = applicant.pastExperience[0]
    const applyAt = new Date(applicant.applyAt)
    const score = applicantScores[applicant.id]

    tableRows.push({
      id: applicant.id,
      name: `${applicant.firstName} ${applicant.lastName}`,
      paredScore: score.overallScore,
      applyTo: applicant.applyTo,
      latestExperience: latestExperience ? `${latestExperience.name} (${latestExperience.duration.toFixed(1)} years)` : '-',
      latestExpDuration: latestExperience ? latestExperience.duration.toFixed(1) : 0,
      averageTurnOver: Number(applicant.averageTurnOver.toFixed(1)),
      jobSource: applicant.source,
      applyAt: applyAt.toDateString(),
    })

    if (applyToData[applicant.applyTo]) {
      applyToData[applicant.applyTo] += 1
    } else {
      applyToData[applicant.applyTo] = 1
    }

    if (latestExperience) {
      if (latestExperienceData[latestExperience.name]) {
        latestExperienceData[latestExperience.name] += 1
      } else {
        latestExperienceData[latestExperience.name] = 1
      }
    }

    if (jobSourceData[applicant.source]) {
      jobSourceData[applicant.source] += 1
    } else {
      jobSourceData[applicant.source] = 1
    }
  })

  const applyToPieChartData: number [] = []
  const applyToPieChartLabels: string [] = []
  Object.keys(applyToData).forEach((position) => {
    applyToPieChartData.push(applyToData[position])
    applyToPieChartLabels.push(position)
  })

  const latestExpPieChartData: number [] = []
  const latestExpPieChartLabels: string [] = []
  Object.keys(latestExperienceData).forEach((position) => {
    latestExpPieChartData.push(latestExperienceData[position])
    latestExpPieChartLabels.push(position)
  })

  const sourcePieChartData: number [] = []
  const sourcePieChartLabels: string [] = []
  Object.keys(jobSourceData).forEach((source) => {
    sourcePieChartData.push(jobSourceData[source])
    sourcePieChartLabels.push(source)
  })

  let selectedApplicantDetailDialog: React.ReactNode = null
  if (allFilters.isFilterSelected && selectedApplicant) {
    selectedApplicantDetailDialog = (
      <ApplicantDetailDialog
        isOpen={isApplicantDetailDialogOpen}
        onClose={closeApplicantDetailDialog}
        applicant={selectedApplicant}
        applicantScores={applicantScores[selectedApplicant.id]}
      />
    )
  }

  return (
    <Content>
      <ChartContainer>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
        >
          <ApplyToPieChartContainer>
            <PieChartTitle>
              Positions Applied
            </PieChartTitle>
            <PieChart
              pieChartData={applyToPieChartData}
              pieChartLabels={applyToPieChartLabels}
            />
          </ApplyToPieChartContainer>

          <LatestExpPieChartContainer>
            <PieChartTitle>
              Applicant Latest Experience
            </PieChartTitle>
            <PieChart
              pieChartData={latestExpPieChartData}
              pieChartLabels={latestExpPieChartLabels}
            />
          </LatestExpPieChartContainer>

          <SourcePieChartContainer>
            <PieChartTitle>
              Job Source
            </PieChartTitle>
            <PieChart
              pieChartData={sourcePieChartData}
              pieChartLabels={sourcePieChartLabels}
            />
          </SourcePieChartContainer>
        </Grid>
      </ChartContainer>

      <AddMoreChartButtonContainer>
        <Button variant="contained">
          <Grid
            container
            direction="row"
            justify="center"
            alignItems="center"
          >
            <AddIcon />
            <AddMoreChartButtonText>Add More Chart</AddMoreChartButtonText>
          </Grid>
        </Button>
      </AddMoreChartButtonContainer>

      <TableContainer>
        <EnhancedTable
          tableTitle={allFilters.isFilterSelected ? 'New Applicants (Store Manager Filter Applied)' : 'New Applicants'}
          tableHead={allFilters.isFilterSelected ? tableHeadWithParedScore : tableHeadWithoutParedScore}
          tableRows={tableRows}
          onClickFilteringButton={openFilterSelectDialog}
          onClickTableRow={onSelectApplicant}
        />
      </TableContainer>

      <FilterSelectDialog
        isOpen={isFilterSelectDialogOpen}
        onClose={closeFilterSelectDialog}
        onSelectFilter={onSelectFilter}
      />

      {selectedApplicantDetailDialog}
    </Content>
  )
}

const Content = styled.div`
  padding: 0;
`

const ChartContainer = styled.div`
  padding: 0 0 30px 0;
  min-width: 1200px;
  max-width: 1300px;
`

const TableContainer = styled.div`
  min-width: 1200px;
`

const ApplyToPieChartContainer = styled.div`

`

const LatestExpPieChartContainer = styled.div`

`

const SourcePieChartContainer = styled.div`

`

const PieChartTitle = styled.div`
  width: 100%;
  text-align: center;
  color: ${COLORS.Steel};
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 15px;
`

const AddMoreChartButtonContainer = styled.div`
  padding: 10px 0 40px 20px;
`

const AddMoreChartButtonText = styled.div`
  padding-left: 10px;
  color: ${COLORS.Chalkboard};
  font-size: 12px;
`

export default NewApplicants
