import React from 'react'
import styled from 'styled-components'
import Grid from '@material-ui/core/Grid'
import EmailIcon from '@material-ui/icons/Email'
import DeleteIcon from '@material-ui/icons/Delete'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import StarIcon from '@material-ui/icons/Star'

import { IApplicant, IApplicantScores } from 'ats/types/applicant'

import COLORS from 'ats/constants/colors'
import SimpleDialog from 'ats/views/basic/dialog/SimpleDialog'
import RadarChart from 'ats/views/basic/chart/RadarChart'

interface IProps {
  isOpen: boolean
  onClose: () => void
  applicant: IApplicant
  applicantScores: IApplicantScores
}

function ApplicantDetailDialog({isOpen, onClose, applicant, applicantScores}: IProps) {
  let scoreData: number []
  if (applicantScores) {
    scoreData = [
      applicantScores.expMatchScore,
      applicantScores.expValueScore,
      applicantScores.careerGrowthScore,
      applicantScores.lessTurnOverScore,
      applicantScores.geoScore
    ]
  } else {
    scoreData = [0, 0, 0, 0, 0]
  }

  const actionButtons = (
    <ActionButtonContainer>
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="center"
      >
        <Tooltip title="Mark As Favorite">
          <IconButton aria-label="favorite">
            <StarIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Send Email">
          <IconButton aria-label="email">
            <EmailIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Archive">
          <IconButton aria-label="archive">
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      </Grid>
    </ActionButtonContainer>
  )

  return (
    <SimpleDialog
      fullWidth
      maxWidth="md"
      isOpen={isOpen}
      onClose={onClose}
    >
      <ApplicantDetailContainer>
        <ApplicantDetailName>
          {applicant.firstName} {applicant.lastName}
        </ApplicantDetailName>

        <Grid
          container
          direction="row"
          justify="center"
          alignItems="flex-start"
        >
          <Grid item xs={1}></Grid>
          <Grid item xs={6}>
            <ApplicantOverallScore>
              Overall Score: {applicantScores ? applicantScores.overallScore.toFixed() : 0}
            </ApplicantOverallScore>

            <RadarChart
              radarChartDatasets={[{
                label: 'Applicant Assessment',
                backgroundColor: 'hsla(208, 100%, 50%, 0.7)',
                borderColor: 'hsla(208, 100%, 50%, 1.0)',
                pointBackgroundColor: 'hsla(208, 100%, 50%, 1.0)',
                data: scoreData
              }]}
              radarChartLabels={[
                ['Experience', 'Match'],
                ['Experience', 'Value'],
                ['Career', 'Growth'],
                ['Less Chance', 'Turn Over'],
                ['Geo', 'Match']
              ]}
            />
            <Spacer />
            {actionButtons}
          </Grid>

          <Grid item xs={5}>
            <ApplicantDetailContent>
              <div>Apply To: {applicant.applyTo}</div>
              <div>Apply At: {(new Date(applicant.applyAt).toDateString())}</div>
              <Spacer />
              <div>Phone: {applicant.phone}</div>
              <div>Email: {applicant.email}</div>
              <Spacer />
              <div>Past Experience:</div>
              {applicant.pastExperience.map((position) => {
                return (
                  <div key={position.name}>
                    {position.name} ({position.duration} years)
                  </div>
                )
              })}
              <Spacer />
              <div>Average Turn Over: {applicant.averageTurnOver.toFixed(1)} Years</div>
              <Spacer />
              <div>More details are coming soon ...</div>
            </ApplicantDetailContent>
          </Grid>
        </Grid>
      </ApplicantDetailContainer>
    </SimpleDialog>
  )
}

const ApplicantDetailContainer = styled.div`
  padding: 20px;
  background-color: ${COLORS.Porcelain};
`

const ApplicantDetailName = styled.div`
  color: ${COLORS.Steel};
  font-size: 25px;
  font-weight: 600;
  padding: 20px 0 20px 0;
`

const ApplicantOverallScore = styled.div`
  color: ${COLORS.Steel};
  font-size: 16px;
  font-style: italic;
`

const ApplicantDetailContent = styled.div`
  color: ${COLORS.Chalkboard};
  padding: 10px 20px;
  font-size: 16px;
`

const ActionButtonContainer = styled.div`
  width: 100%;
  padding: 0 0 20px 0;
`

const Spacer = styled.div`
  width: 1px;
  height: 1px;
  padding: 10px 10px 0 0;
`

export default ApplicantDetailDialog

