import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import styled from 'styled-components'
import { withStyles, makeStyles } from '@material-ui/core/styles'
import { deepPurple } from '@material-ui/core/colors'
import Avatar from '@material-ui/core/Avatar'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Slider from '@material-ui/core/Slider'
import AddIcon from '@material-ui/icons/Add'
import EmailIcon from '@material-ui/icons/Email'
import DeleteIcon from '@material-ui/icons/Delete'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'
import StarIcon from '@material-ui/icons/Star'

import { IApplicant, IApplicantScores } from 'ats/types/applicant'

import COLORS from 'ats/constants/colors'
import PastExperienceFilters from 'ats/pages/PastExperienceFilters'
import { setMinMaxScores, selectAllFilters } from 'ats/slices/filter'
import { getApplicantScores } from 'ats/util'

import ApplicantDetailDialog from './ApplicantDetailDialog'

interface IProps {
  newApplicants: IApplicant []
}

const PrettoSlider = withStyles({
  root: {
    color: '#52af77',
    height: 8,
  },
  thumb: {
    height: 24,
    width: 24,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -8,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 4px)',
  },
  track: {
    height: 8,
    borderRadius: 4,
  },
  rail: {
    height: 8,
    borderRadius: 4,
  },
})(Slider);

function FilterView({ newApplicants }: IProps) {
  const [isApplicantDetailDialogOpen, setIsApplicantDetailDialogOpen] = useState(false)
  const [selectedApplicant, setSelectedApplicant] = useState<IApplicant | null>(null)
  const [matchedApplicants, setMatchedApplicants] = useState<IApplicant []>([])
  const [applicantScores, setApplicantScores] = useState<{ [id: number]: IApplicantScores }>({})
  const allFilters = useSelector(selectAllFilters)
  const styleClasses = useStyles()
  const dispatch = useDispatch()

  useEffect(() => {
    const applicantScores = getApplicantScores(newApplicants, allFilters)
    const matched = newApplicants.filter((applicant) => {
      const score = applicantScores[applicant.id]
      if (score) {
        return (score.overallScore >= allFilters.minScore && score.overallScore <= allFilters.maxScore)
      } else {
        return false
      }
    })
    const sortedMatched = matched.sort((first, second) => {
      const firstScore = applicantScores[first.id]
      const secondScore = applicantScores[second.id]
      if (!firstScore) { return 1 }
      if (!secondScore) { return -1 }
      return secondScore.overallScore - firstScore.overallScore
    })

    setMatchedApplicants(sortedMatched)
    setApplicantScores(applicantScores)
  }, [newApplicants, allFilters])

  const onScoresChange = (event: any, newScore: any) => {
    dispatch(setMinMaxScores({ minScore: newScore[0], maxScore: newScore[1] }))
  }

  const openApplicantDetailDialog = (applicant: IApplicant) => {
    setSelectedApplicant(applicant)
    setIsApplicantDetailDialogOpen(true)
  }

  const closeApplicantDetailDialog = () => {
    setIsApplicantDetailDialogOpen(false)
  }

  const totalNewApplicantCount = newApplicants.length
  const matchedApplicantElements = matchedApplicants.map((applicant) => {
    const score = applicantScores[applicant.id]
    const overallScore = score ? score.overallScore.toFixed() : 0
    return (
      <Applicant
        key={applicant.id}
        onClick={() => {
          openApplicantDetailDialog(applicant)
        }}
      >
        <Grid
          container
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          <Avatar className={styleClasses.purple}>{overallScore}</Avatar>
          <ApplicantName>
            {applicant.firstName} {applicant.lastName}
          </ApplicantName>
        </Grid>
      </Applicant>
    )
  })

  const actionButtons = (
    <ActionButtonContainer>
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="center"
      >
        <Tooltip title="Mark As Favorite">
          <IconButton aria-label="favorite">
            <StarIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Send Email">
          <IconButton aria-label="email">
            <EmailIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Archive">
          <IconButton aria-label="archive">
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      </Grid>
    </ActionButtonContainer>
  )

  let selectedApplicantDetailDialog: React.ReactNode = null
  if (selectedApplicant) {
    selectedApplicantDetailDialog = (
      <ApplicantDetailDialog
        isOpen={isApplicantDetailDialogOpen}
        onClose={closeApplicantDetailDialog}
        applicant={selectedApplicant}
        applicantScores={applicantScores[selectedApplicant.id]}
      />
    )
  }

  return (
    <Content>
      <TitleContainer>
        <FilterForSpan>Filter for</FilterForSpan>
        <TitleSpan>Store Manager</TitleSpan>
      </TitleContainer>

      <FilterContainer>
        <FilterResultContainer>
          <TotalApplicantCount>
            <h4>Total Applicant Count</h4>
            <div>{totalNewApplicantCount}</div>
          </TotalApplicantCount>
          <MatchedApplicantCount>
            <h4>Matched Applicant Count</h4>
            <div>{matchedApplicants.length}</div>
          </MatchedApplicantCount>

          <ScoreSliderContainer>
            <ScoreSliderText>
              Min Score: {allFilters.minScore}
            </ScoreSliderText>
            <ScoreSliderText>
              Max Score: {allFilters.maxScore}
            </ScoreSliderText>
            <ScoreSliderWrapper>
              <PrettoSlider value={[allFilters.minScore, allFilters.maxScore]} onChange={onScoresChange} />
            </ScoreSliderWrapper>
          </ScoreSliderContainer>

          {actionButtons}

          <MatchedApplicants>
            {matchedApplicantElements}
          </MatchedApplicants>
        </FilterResultContainer>

        <FilterDetailContainer>
          <PastExperienceFilterContainer>
            <FilterName>Applicant Past Experience Filters</FilterName>
            <FilterDescription>
              Drag bubbles and the ends of bars horizontally in the chart to set the past experience criteria for applicants.
            </FilterDescription>
            <Spacer />
            <PastExperienceFilters />
          </PastExperienceFilterContainer>

          <TurnOverFilterContainer>
            <FilterName>Applicant Turn Over Filters</FilterName>
            <FilterDescription>
              This filter is coming soon ...
            </FilterDescription>
          </TurnOverFilterContainer>

          <GeolocationFilterContainer>
            <FilterName>Applicant Geolocation Filters</FilterName>
            <FilterDescription>
              This filter is coming soon ...
            </FilterDescription>
          </GeolocationFilterContainer>

          <AddMoreFilterButtonContainer>
            <Button variant="contained">
              <Grid
                container
                direction="row"
                justify="center"
                alignItems="center"
              >
                <AddIcon />
                <AddMoreFilterButtonText>Add More Filter</AddMoreFilterButtonText>
              </Grid>
            </Button>
          </AddMoreFilterButtonContainer>
        </FilterDetailContainer>
      </FilterContainer>

      {selectedApplicantDetailDialog}
    </Content>
  )
}

const useStyles = makeStyles((theme) => ({
  purple: {
    color: theme.palette.getContrastText(deepPurple[500]),
    backgroundColor: deepPurple[500],
  },
}))

const Content = styled.div`
  padding: 20px;
  position: relative;
  width: 1600px;
`

const TitleContainer = styled.div`
  padding: 0 0 40px 0;
`

const FilterForSpan = styled.span`
  color: ${COLORS.Steel};
  font-style: italic;
  font-size: 12px;
`

const TitleSpan = styled.span`
  color: ${COLORS.Steel};
  font-size: 35px;
  font-weight: 600;
  padding-left: 10px;
`

const FilterContainer = styled.div`
  position: relative;
  width: 100%;
`

const FilterResultContainer = styled.div`
  position: absolute;
  width: 250px;
  min-height: 800px;
  padding: 0 0 0 30px;
  top: 0;
  left: 0;
  border-right: 1px solid ${COLORS.Smoke};
`

const FilterDetailContainer = styled.div`
  position: absolute;
  top: 0;
  left: 300px;
`

const TotalApplicantCount = styled.div`
  border-bottom: 1px solid ${COLORS.Smoke};
  width: 100%;
  text-align: center;
  padding: 0 20px 20px 20px;
  color: ${COLORS.Chalkboard};
`

const MatchedApplicantCount = styled.div`
  width: 100%;
  text-align: center;
  padding: 0 20px 20px 20px;
  color: ${COLORS.Chalkboard};
`

const MatchedApplicants = styled.div`
`

const Applicant = styled.div`
  width: 100%;
  padding: 20px;
  border-bottom: 1px solid ${COLORS.Ash};
  cursor: pointer;
`

const ApplicantName = styled.div`
  color: ${COLORS.Chalkboard};
  padding-left: 10px;
  word-wrap: break-word;
  max-width: 130px;
`

const FilterName = styled.div`
  color: ${COLORS.Steel};
  font-size: 20px;
  font-weight: 600;
  padding: 10px 0;
`

const FilterDescription = styled.div`
  color: ${COLORS.Steel};
  font-size: 12px;
  font-style: italic;
`

const PastExperienceFilterContainer = styled.div`
  padding-bottom: 40px;
  border-bottom: 1px solid ${COLORS.Ash};
`

const TurnOverFilterContainer = styled.div`
  padding: 30px 0 40px 0;
  border-bottom: 1px solid ${COLORS.Ash};
`

const GeolocationFilterContainer = styled.div`
  padding: 30px 0 40px 0;
  border-bottom: 1px solid ${COLORS.Ash};
`

const AddMoreFilterButtonContainer = styled.div`
  padding: 40px 0 40px 0;
`

const AddMoreFilterButtonText = styled.div`
  padding-left: 10px;
  color: ${COLORS.Chalkboard};
  font-size: 12px;
`

const ScoreSliderContainer = styled.div`
  width: 100%;
  padding: 20px 10px 20px 10px;
`

const ScoreSliderText = styled.div`
  text-align: center;
  color: ${COLORS.Chalkboard};
`

const ScoreSliderWrapper = styled.div`
  width: 100%;
  padding-right: 20px;
`

const ActionButtonContainer = styled.div`
  width: 100%;
  padding: 0 0 20px 0;
`

const Spacer = styled.div`
  width: 1px;
  height: 1px;
  padding: 10px 10px 0 0;
`

export default FilterView
